import twitterLogo from './assets/twitter-logo.svg';
import React, { useEffect } from 'react';

import './App.css';

// Constants
const TWITTER_HANDLE = 'ronkips01';
const TWITTER_LINK = `https://twitter.com/${TWITTER_HANDLE}`;

const App = () => {
  //function that checks if the wallet is connected
  const checkIfWalletIsConnected = async () => {
    try {
      const { solana} = window;

      if ( solana ) {
        if (solana.isPhantom) {
          console.log('Phantom wallet found');
        }
      } else {
        alert('Solana object not found! Please get a phantom wallet ');
      }
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    const onLoad = async () => {
      await checkIfWalletIsConnected();
    };
    window.addEventListener('load', onLoad);
    return () => window.removeEventListener('load', 'onLoad');
  }, []);
  return (
    <div className="App">
      <div className="container">
        <div className="header-container">
          <p className="header">ðŸ–¼ My GIF Portal</p>
          <p className="sub-text">
            Please view your GIF collection in the metaverse âœ¨
          </p>
        </div>
        <div className="footer-container">
          <img alt="Twitter Logo" className="twitter-logo" src={twitterLogo} />
          <a
            className="footer-text"
            href={TWITTER_LINK}
            target="_blank"
            rel="noreferrer"
          >{`built on @${TWITTER_HANDLE}`}</a>
        </div>
      </div>
    </div>
  );
};

export default App;
